@import "../../../css-config/mixins.scss";

.shop-by-category {
  margin: 25px 0;
  @include md {
    margin: 50px 0;
  }
  .categories {
    display: flex;
		justify-content: center;
    flex-flow: wrap;
    gap: 10px;
		@include md{
			padding-left: 10px;
		}
    .category {
      background-color: black;
      width: calc(50% - 10px);
      overflow: hidden;
      cursor: pointer;
      @include md {
        width: calc(25% - 10px);
      }
      img {
        width: 100%;
        display: block;
        transition: all ease 0.3s;
      }
      &:hover {
        img {
          transform: scale(1.2);
        }
      }
    }
  }
}
